/**
 * AdminNoticesManager - Admin Styles
 */

#wp-admin-bar-anm_notification_count {
  > a {
	cursor: not-allowed;
  }

  &.has-data {
	> a {
	  cursor: pointer;
	}
  }
}

.anm-notification-counter {
  background-color: #ca4a1f;
  border-radius: 50% !important;
  box-sizing: border-box !important;
  font-size: 11px !important;
  height: 18px !important;
  line-height: 1.6 !important;
  min-width: 18px;
  text-align: center;
  vertical-align: text-bottom;
  display: inline;
  padding: 4px 7px 4px 6px !important;
  color: #fff;
  margin-left: 7px !important;
  opacity: 0;
  transition: all 0.2s ease-in-out !important;

  > span {
	line-height: 100% !important;
  }

  &.display {
    opacity: 1;
    transition: all 0.2s ease-in-out !important;
  }
}

.anm-pointer {
  .wp-pointer-arrow {
	left: 50%;
	margin-left: -7px;
  }
}


#anm-container-slide-in {
  padding-top: 34px;
  height: 100vh;
  position: fixed;
  background: #222;
  right: -620px;
  z-index: 1000;
  top: 0;
  transition: all 0.3s ease-in-out;
  width: 620px;

  &.show {
    right: 0px;
    transition: all 0.2s ease-in-out;
  }
  .notice, div.error, div.updated {
      padding: 1px 12px !important;
  }
}

@media all and (max-width: 540px) {
  #anm-container-slide-in {
    right: -330px;
    width: 330px;
  }
}

.anm-notice-timestap {
  border-top: 1px solid #eee;
  padding: 9px 0 10px;
  font-size: 11px;
  margin-top: 14px;
  display: block;

  span {
    font-style: italic;
  }

  a {
    float: right;
  }
}

#anm-slide-in-content {
  max-height: calc( 100vh - 100px );
  overflow-y: auto;
  direction: ltr;
  scrollbar-color:#333;
  scrollbar-width: thin;

  &::-webkit-scrollbar {
    width: 20px;
  }
  
  &::-webkit-scrollbar-track {
    background-color: #222;
    border-radius: 100px;
  }
  
  &::-webkit-scrollbar-thumb {
    border-radius: 100px;
    border: none;
    border-left: 0;
    border-right: 0;
    background-color: #333;
  }  
}

#anm-system-notices, #anm-error-notices, #anm-warning-notices, #anm-success-notices, #anm-information-notices {
  .notice {
    margin-bottom: 15px;

    p {
      width: calc( 100% - 38px);
      font-size: 13px;
      line-height: 22px;
    }

    &.is-dismissible {
      padding-right: 12px !important;
    }
  }
  .notice-addon-available {
    display: block;
  }
}

#anm-notice-purged-text {
  opacity: 0;
  color: green;
  display: inline-block;
  font-size: 12px;
  padding: 7px 12px;
  transition: all 0.2s ease-in-out;
}

#anm-notice-purged-text.visible {
  opacity: 1;
  transition: all 0.2s ease-in-out;
}

.settings_page_admin_notices_settings #setting-error-settings_updated + #setting-error-settings_updated {
  display: none;
}